.table {
	border-collapse: collapse;
	width: 100%;
	color: $primary-gray;
	font-family: "RobotoRegular", sans-serif;
	font-size: 14px;

	&__head {
		text-align: left;
		border-radius: 8px;
		background-color: $border;


		&-row {
			padding-top: 25px;

			th {
				padding: 14px 0;
				color: $primary-gray;
				font-family: "RobotoRegular", sans-serif;
				font-size: 12px;

				&:first-child {
					border-radius: 8px 0 0 8px;
					padding: 14px 0 14px 21px; }

				&:last-child {
					border-radius: 0 8px 8px 0;
					padding: 14px 21px 14px 0; } } } }

	&__body {

		td {
			padding: 7px 0; }

		tr:first-child td {
			padding-top: 30px; }


		tr:last-child td {
			padding-bottom: 15px; }

		tr:last-child {
			border-bottom: 1px solid $border; }

		tr td:first-child {
			width: 50%;
			padding-right: 100px; } }

	&_small-text {
		font-size: 12px;
		color: $secondary-gray; }


	&_no-border {

		tr:last-child {

			border-bottom: 1px solid transparent; } }


	&-delivery {
		border-collapse: collapse;
		width: 100%;
		color: $primary-gray;
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;

		td {
			padding: 10px 0; }

		tr:not(:last-child) {
			border-bottom: 1px solid $border; }

		&_padding-top_20 {

			td {
				padding-top: 20px; } } } }





