.certificate-sorter {
	display: flex;
	align-items: center;
	margin: 15px 0;

	&__select {
		border: none;
		background-color: transparent;
		cursor: pointer;
		color:  $page-text;
		font-size: 14px;
		font-family: "RobotoRegular", sans-serif;
		width: 100%;
		height: 100%;
		border-radius: 4px;
		border: 1px solid $border;
		padding-left: 8px;
		box-sizing: border-box;


		&-pseudo {
			display: flex;
			align-items: center;
			width: 168px;
			height: 30px; } }



	&__form {
		display: flex;
		align-items: center;
		margin-bottom: 23px;
		margin-top: 32px; }

	&__label {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		color: $primary-gray;
		letter-spacing: 0.02em;
		margin-right: 8px;
		display: flex;
		align-items: center; } }
