.m-content {

	width: 100%;
	display: flex;
	margin-top: 12px;
	flex-wrap: wrap;
	display: none;

	@media screen and (max-width:992px) {
		display: flex; }


	&__item {
		width: 50%;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		margin-bottom: 25px; }

	&__title {
		text-align: center;
		font-size: 12px;
		max-width: 140px; } }
