.phone {
	font-family: "RobotoMedium", Arial, sans-serif;
	font-size: 20px;
	line-height: 1;
	color: $primary-gray;
	text-decoration: none;
	transition: color .4s ease-out;

	&:not(:last-child) {
		margin-bottom: 24px; }

	&:hover {
		color: $primary-gray_hover; }


	&_icon {
		position: relative;
		padding-left: 33px;

		&::before {
			position: absolute;
			content: '';
			left: 0;
			width: 19px;
			height: 19px;
			background: url('../img/phone.svg') transparent no-repeat center; } } }

.mobile-phone_icon {
	position: relative;
	padding-left: 33px;

	&::before {
		position: absolute;
		content: '';
		left: 4px;
		width: 13px;
		height: 22px;
		background: url('../img/mobile_phone.svg') transparent no-repeat center; } }

