.delivery {

	&__partner {
		max-width: 329px;
		width: 100%;

		&-wrap {
			max-width: 329px;
			width: 100%;
			margin-right: 3%;

			@media screen and (max-width:580px) {
				margin-right: 0; } } }


	&_margin-top_40 {
		margin-top: 40px; }



	&__inner {

		display: flex;
		flex-wrap: wrap;

		@media screen and (max-width:580px) {
			justify-content: space-between; }



		&_margin_35 {
			margin-top: 35px;
			margin-bottom: 35px; }

		&_space-between {
			justify-content: space-between;
			max-width: 730px; } }



	&__item {

		width: 38%; }


	&__partner {


		&-info {
			max-width: 355px; } }

	&__list {
		margin-top: 30px;
		margin-bottom: 30px;

		&-item {
			margin-bottom: 5px; } }

	&-company {

		&__img {
			max-width: 100%; }


		&__item {

			display: flex;
			min-height: 110px;
			align-items: center;

			border-top: 1px solid $border;

			&:last-child {
				border-bottom: 1px solid $border; }


			&-inner {
				width: 46%;

				@media screen and (max-width:580px) {
					margin-right: 20px; } } }


		&_margin {
			margin-top: 35px; } } }

