.page {
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	max-width: 100%;
	font-family: "RobotoRegular", Arial, sans-serif;
	font-size: 14px;
	line-height: 1.2rem;
	color: $primary-gray;
	background-color: $page-background; }

.main {
	margin-top: 15px;
	padding-bottom: 116px;

	@media screen and (max-width:992px) {
		padding-bottom: 42px; }

	&__container {
		max-width: 1200px;
		width: 100%;
		padding: 0 15px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		margin-left: auto;
		margin-right: auto; } }

.content {
	display: flex;
	width: 100%;
	justify-content: space-between;
	box-sizing: border-box;

	&__container {
		max-width: 900px;
		width: 100%;

		&_no-sidebar {
			margin-left: auto; } } }

.sidebar {
	width: 100%;
	max-width: 250px;
	margin-right: 20px; }




.text-bold {
	font-family: "RobotoBold", Arial, sans-serif; }


.padding-bottom_15 {
	padding-bottom: 15px; }



.text-light {
	color: $secondary-gray; }

.bold-text {
	font-family: "RobotoBold", sans-serif;
	color: $primary-gray; }

.text-uppercase {
	font-family: "RobotoMedium", sans-serif;
	line-height: 1.2rem;
	letter-spacing: 0.08rem;
	color: $primary-gray;
	font-size: 14px;
	text-transform: uppercase; }

.orange-text {
	color: $primary-color; }

.orange-text-medium {
	color: $primary-color;
	font-family: "RobotoMedium", sans-serif; }


