.brand {

	width: 100%;
	margin-bottom: 20px;


	&__list {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }

	&__item {
		width: 18.5%;
		height: 76px;
		margin-bottom: 15px;
		padding: 6px;
		box-sizing: border-box;
		border: 1px solid $cart-value;
		border-radius: 8px;
		background-color: #fff;
		overflow: hidden;

		display: flex;
		justify-content: center;
		align-items: center; }

	&__img {

		max-width: 100%;
		max-height: 100%; } }

