
.footer {
	background-color: $footer_background;
	padding: 50px 0 80px;

	position: relative;
	display: flex;
	justify-contnet: space-between;

	&__container {
		max-width: 1200px;
		width: 100%;
		padding: 0 15px;
		box-sizing: border-box;
		display: flex;
		justify-content: space-between;
		margin-left: auto;
		margin-right: auto;
		flex-wrap: wrap; }

	&::after {
		position: absolute;
		z-index: 2;
		content: '';
		width: 100%;
		background-color: #282828;
		height: 12px;
		bottom: 0;
		left: 0; }


	&__item {
		display: flex;
		flex-direction: column;
		width: 22%;

		@media screen and (max-width:992px) {
			width: 100%; }

		&_size_s {
			width: 18%; } }

	&__contacts {


		margin-bottom: 30px;

		&-item {
			font-family: "RobotoBold", sans-serif;
			line-height: 0.9em;
			margin-top: 0;
			margin-bottom: 19px;
			color: $primary-color;
			font-size: 14px; } }

	&__info {
		display: flex;
		flex-direction: column;
		color: $footer-text;
		line-height: 1.2em;
		letter-spacing: 0.02em;
		max-width: 203px; }

	&__products-link {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		color: $footer-products;
		text-decoration: none;
		transition: color .4s ease-out;

		&:not(:last-child) {
			margin-bottom: 13px; }

		&:hover {
			color: $primary-color; } }

	&__nav {
		font-family: "RobotoBold", sans-serif;
		font-size: 12px;
		line-height: 1.2;
		letter-spacing: 0.14em;
		color: $footer-nav;
		text-transform: uppercase;
		text-decoration: none;
		transition: color .4s ease-out;

		&:not(:last-child) {
			margin-bottom: 20px; }

		&:hover {
			color: $primary-color; } }

	&__requisites {
		display: flex;
		flex-direction: column;
		font-family: "RobotoRegular", sans-serif;
		font-size: 12px;
		color: $footer-text;
		line-height: 1.2;
		letter-spacing: 0.02em;

		&-title {
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			color: $footer-nav;
			line-height: 1;
			letter-spacing: 0.02em;
			margin-bottom: 14px;
			margin-top: 5px; }

		&-img {
			margin-bottom: 35px; }

		&_mobile {
			display: none;

			@media screen and (max-width:992px) {
				display: flex;
				max-width: 271px;
				margin-bottom: 30px; } } }




	&_company {
		margin-bottom: 20px; }

	&__phone {
		text-decoration: none;
		transition: color .4s ease-out;

		&:hover {
			color: $primary-color_hover; } }

	&_text_light {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		color: $footer-text; } }


.footer_phone_icon {
	padding-left: 27px;
	position: relative;

	&::before {
		position: absolute;
		content: '';
		left: 0;
		top: 2px;
		width: 15px;
		height: 15px;
		background: url('../img/footer_phone.svg') transparent no-repeat center; } }
