.m-menu {
	background-color: $footer_background;
	min-height: 48px;
	justify-content: center;
	display: flex;
	width: 100%;
	align-items: center;
	display: none;

	@media screen and (max-width:992px) {
		display: flex; }

	&__container {
		max-width: 1200px;
		width: 100%;
		padding: 0 15px;
		box-sizing: border-box;
		display: flex;
		flex-direction: row;
		margin-left: auto;
		margin-right: auto;
		height: 100%;
		justify-content: space-between; }



	&__user-block {
		display: flex;
		align-items: center; }


	&__nav-block {
		display: flex;
		align-items: center; }

	&__cart {
		display: flex;
		position: relative; }

	&__cart-sum {
		position: absolute;
		content: '';
		width: 14px;
		height: 14px;
		background-color: #fff;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 9px;
		font-family: "RobotoBold", sans-serif;
		color:  $primary-color;
		right: -3px;
		top: -3px; }

	&_margin-right {
		margin-right: 18px; } }



.m-hamburger {
	width: 24px;
	height: 14px;
	position: relative;

	cursor: pointer;
	margin-right: 12px;

	&::before {
		position: absolute;
		content: '';
		width: 24px;
		height: 2px;
		background-color: #fff;
		bottom: 0; }

	&::after {
		position: absolute;
		content: '';
		width: 24px;
		height: 2px;
		background-color: #fff;
		bottom: 6px; }


	&__bar {
		width: 24px;
		height: 2px;
		border-radius: 4px;
		background-color: #fff; } }







