.catalog-form {
	width: 100%;
	box-sizing: border-box;
	padding: 0 0 20px;
	border: 1px solid $border;
	border-radius: 8px;
	background-color: #fff;
	margin-top: 20px;

	&__title {
		font-family: "RobotoBold", sasn-serif;
		font-size: 14px;
		color: $primary-gray;
		margin-bottom: 17px;
		display: inline-block; }


	&__item {
		position: relative;
		margin: 0;
		padding: 14px 20px;

		&::after {
			position: absolute;
			content: '';
			width: 100%;
			bottom: 0;
			left: 0;
			height: 1px;
			background-color: $border; } }


	&__inner {
		padding: 20px 20px 0;
		width: 100%;
		display: inline-block;
		box-sizing: border-box; }



	&__button {
		box-sizing: border-box;
		width: 100%;
		line-height: 36px; }

	&__manufact {
		padding: 0 20px;
		position: relative;

		&::after {
			position: absolute;
			content: '';
			width: 100%;
			bottom: 0;
			left: 0;
			height: 1px;
			background-color: $border; } }


	&__manufact-item {
		display: flex;
		width: 100%;
		padding: 13px 0;
		align-items: center; }

	&__arrow {
		margin-left: 12px; }



	&__manufact-inner {
		padding: 0;
		margin: 0;
		display: flex;
		flex-wrap: wrap;
		padding-bottom: 12px;
		display: none;

		&_active {
			display: flex; } }

	&__manufact-inner-item {
		list-style: none;
		padding: 6px 0;
		width: 50%; } }




.form-wrapper {
	display: none;

	@media screen and (max-width:992px) {
		display: flex; } }





