.slideshow {
	width: 100%;
	background-color: #fff;
	border: 1px solid $border;
	border-radius: 8px;
	padding: 12px 18px;
	display: flex;
	box-sizing: border-box;




	&__fullsize {

		padding: 38px 68px 26px 74px;
		box-sizing: border-box;

		&-img {
			max-width: 362px;
			max-height: 283px;
			width: 100%;
			height: 100%; } }

	&__thumbs {
		padding: 0;
		margin-left: auto;

		&-img {
			max-width: 51px;
			max-height: 40px; }

		&-link {
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center; }

		&-item {
			list-style: none;
			width: 60px;
			height: 50px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 8px;
			border: 1px solid transparent;

			&_active {
				border: 1px solid $border; } } }


	&_col_6 {
		width: 62.5%;

		@media screen and (max-width:992px) {
			width: 100%; } } }

