.more-info {

	&-title {
		cursor: pointer;
		display: inline-block;
		padding-bottom: 7px;
		border-bottom: 1px dashed $primary-gray;
		text-decoration: none;

		&:hover {
			border-bottom: 1px dashed transparent; } }


	&__text {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		line-height: 1.2rem;
		color: $primary-gray;
		margin-bottom: 28px;
		max-width: 756px; } }

