.time_icon {
	padding-left: 27px;
	position: relative;

	&::before {
		position: absolute;
		content: '';
		left: 0;
		top: -2px;
		width: 16px;
		height: 16px;
		background: url('../img/time_icon.svg') transparent no-repeat center; } }


