.good-tab {
	margin-bottom: 30px;

	&__column {
		display: flex;
		flex-direction: column;

		&_margin-bottom_8 {
			margin-bottom: 8px; }

		&_margin-bottom_18 {
			margin-bottom: 18px; } }


	&__list {
		margin: 0;

		&::after {
			background-color: transparent; } }

	&__content {
		background-color: #fff;
		border: 1px solid $border;
		border-radius: 8px;
		border-top-left-radius: 0;
		margin: 0;
		padding: 30px 12px; }

	&_no-margin {
		margin: 0; }



	&__container {
		width: 66.66%;

		box-sizing: border-box; }

	&__sidebar {
		width: 30%;
		position: relative;
		box-sizing: border-box;
		padding-left: 20px;
		padding-right: 14px;

		&::before {
			position: absolute;
			content: '';
			width: 1px;
			height: 100%;
			background-color: $border;
			left: 0; }

		.good:last-of-type {
			margin-bottom: 24px; } }


	&__descr {
		margin-top: 0; }


	&__text {
		margin-bottom: 8px;
		margin-top: 8px; }



	&__doc {
		max-width: 89px; }

	&__inner {
		display: flex;

		&_margin-top_24 {
			margin-top: 24px; }

		&-wrap {
			display: flex;
			flex-direction: column;

			&_size_s {
				max-width: 94px;
				margin-right: 50px;

				&:last-child {
					margin-right: 0; } }

			&_margin-bottom_12 {
				margin-bottom: 12px; }

			&_margin-bottom_20 {
				margin-bottom: 20px; } }

		&-item {

			&_size_m {
				max-width: 290px;
				width: 100%;
				flex-wrap: wrap; } }

		&-list {
			padding-left: 0;

			&-item {
				list-style: none;
				padding-left: 20px;
				margin-bottom: 8px;
				position: relative;

				&::before {
					position: absolute;
					content: '';
					width: 8px;
					height: 8px;
					left: 0;
					top: 6px;
					border-radius: 50%;
					background-color: $primary-gray; } } } }


	&__title {
		font-family: "RobotoMedium", Arial, sans-serif;
		font-size: 20px;
		line-height: 1;
		color: $primary-gray;
		margin-bottom: 20px;
		font-weight: normal;
		margin-top: 0;

		&_margin-left_14 {
			margin-left: 14px; } }

	&__country {
		margin-right: 12px; }

	&__button {
		max-width: 183px; } }

.good-tab__inner-list .good-tab__inner-list-item:last-child {
	margin-bottom: 0; }
