.breadcrumbs {
	padding: 0;
	display: flex;
	margin-top: 15px;
	flex-wrap: wrap;

	&__item {
		list-style: none;
		position: relative;
		display: flex;

		&:not(:last-child) {
			margin-right: 18px;

			&::after {
				position: absolute;
				content: '';
				width: 8px;
				height: 14px;
				top: 3px;
				right: -13px;
				background: url('../img/breadcrumb_next.svg') no-repeat center; } } }

	&__link {
		color: $primary-blue;
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		line-height: 1.2;
		letter-spacing: 0.02em;

		&:hover {
			text-decoration: none; } }

	&_margin-bottom_25 {
		margin-bottom: 25px; } }
