.m-992_no {
	@media screen and (max-width:992px) {
		display: none !important; } }

.m-1200_no {
	@media screen and (max-width:1200px) {
		display: none !important; } }



.top-menu__link {
	@media screen and (max-width: 1200px) {
		font-size: 10px; } }


.top-menu__cart-value, .top-menu__cart-text {
	@media screen and (max-width: 1200px) {
		font-size: 10px; } }

.top-menu__cart-container {
	@media screen and (max-width: 1200px) {
		flex-wrap: wrap; } }

.top-menu__item {
	@media screen and (max-width:1100px) {
		min-width: 100px; } }

.phone {
	@media screen and (max-width: 1200px) {
		font-size: 14px; } }

.content__container_no-sidebar {
	@media screen and (max-width: 992px) {
		margin-left: auto;
		margin-right: auto; } }





