.catalog-menu {
	width: 100%;
	display: flex;
	background-color: #fff;
	border-radius: 8px;
	border: 1px solid $border;

	&__item {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		line-height: 1.2em;
		color: $primary-gray;
		text-decoration: none;
		padding: 10px 12px;
		position: relative;

		&_icon {
			margin-right: 38px;
			position: relative;

			&::before {
				position: absolute;
				width: 14px;
				height: 8px;
				content: '';
				right: -6px;
				top: 14px;
				background: url('../img/more-arrow.svg') no-repeat center; } }


		&:hover {
			text-decoration: underline; }

		&::after {
			position: absolute;
			content: '';
			width: 1px;
			height: 100%;
			background-color: $border;
			right: 0;
			top: 0; }

		&:last-child::after {
			display: none; } } }
