.brands-abc {
	margin: 0;
	padding: 0;
	display: flex;
	flex-wrap: wrap;
	margin-top: 30px;
	padding-bottom: 10px;
	position: relative;
	z-index: 1;

	&::after {
		position: absolute;
		content: '';
		width: 100%;
		height: 1px;
		background-color: $border;
		bottom: 0;
		z-index: 2; }

	&__list {
		padding: 0;
		margin-top: 38px;
		margin-bottom: 30px;
		box-sizing: border-box;
		display: flex;
		flex-wrap: wrap; }

	&__title {
		font-family: "RobotoBold", sans-serif;
		font-size: 14px;
		color: $primary-color;
		margin-bottom: 20px;
		display: block; }


	&__item {
		list-style: none;
		width: 25%;
		margin-bottom: 25px;
		box-sizing: border-box;
		padding-right: 20px;

		&_col_2 {
			@media screen and (max-width:767px) {
				width:  50%; } }

		&_col_1_sm {
			@media screen and (max-width:360px) {
				width:  100%; } } }


	&-item {


		&__sum {
			text-decoration: none;
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			line-height: 1.2em;
			color: $light-gray-text;
			display: inline-block;
			cursor: pointer; }

		&__inner {

			&-list {
				padding-left: 0;
				display: flex;
				flex-direction: column; }

			&-item {
				padding-left: 0;
				list-style: none;
				margin-bottom: 10px;
				position: relative;
				padding-left: 25px; }



			&-link {
				font-family: "RobotoRegular", sans-serif;
				font-size: 14px;
				line-height: 1.2em;
				letter-spacing: 0.02em;
				color: $primary-blue;
				position: relative;
				margin-right: 5px;
				display: inline-block;

				&:hover {
					text-decoration: none; } } } } }


.brands-abc-inner {

	&__list {
		display: block;
		padding-left: 0; }

	&__item {
		list-style: none;
		margin-bottom: 5px; }

	&__link {
		color: $primary-gray;
		text-decoration: none;

		&:hover {
			text-decoration: underline; } } }
