.range {

	&__controls {
		position: relative;
		margin-bottom: 12px; }

	&__value-wrap {
		display: flex;
		justify-content: space-between; }

	&__toggle {
		position: absolute;
		width: 6px;
		height: 15px;
		content: '';
		background: url('../img/toggle.svg') no-repeat left center;
		z-index: 10;
		cursor: pointer; }

	&__toggle-min {
		left: -3px; }

	&__toggle-max {
		left: calc(100% - 3px); }

	&__price-controls {
		display: flex;
		justify-content: space-between;
		margin-bottom: 15px; }

	&__price-labels {
		display: flex;
		align-items: center; }

	&__scale {
		height: 4px;
		width: 100%;
		background-color: $border; }

	&__bar {
		background-color: $primary-color;
		height: 4px;
		width: 100%; }





	&__price-input {
		width: 63px;
		height: 30px;
		border: 1px solid $border;
		border-radius: 4px;
		color: $primary-gray;
		font-size: 14px;
		box-sizing: border-box;
		padding-left: 8px;
		text-align: left;
		margin-left: 10px;
		-moz-appearance: textfield;


		&::-webkit-outer-spin-button {
			-webkit-appearance: none;
			margin: 0; }

		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0; }


		&:focus {
			outline: none; }

		&_max {
			margin-right: 8px; } } }
