.top-menu {
	background-color: $top-menu_background;

	&-container {
		max-width: 1200px;
		padding: 0 15px;
		width: 100%;
		box-sizing: border-box;
		margin-right: auto;
		margin-left: auto;
		display: flex;
		position: relative; }


	&__nav {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		min-height: 48px; }

	&__list {
		margin: 0;
		padding: 0;
		display: flex; }

	&__item {
		list-style: none;
		min-width: 122px;
		height: 100%;
		max-width: 132px; }

	&__sub {

		&-catalog {
			position: absolute;
			content: '';
			background-color: #fff;
			width: 245px;
			top: 100%;
			padding: 0;
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
			border: 1px solid #eeeeee;
			box-shadow: 0 2px 10px rgba(0,0,0,.1);
			display: none;
			z-index: 100; }

		&-item {
			display: flex;
			align-items: center;
			list-style: none;
			height: 68px;
			border-bottom: 1px solid #EEEEEE;
			box-sizing: border-box;
			border-left: 2px solid transparent;

			&:last-child {
				border-bottom: none; }

			&:hover {
				background-color: #f9f9f9;
				border-left: 2px solid $primary-color; }


			&:hover .top-menu__sub-inner {
				display: block; } }


		&-icon {
			height: 37px;
			width: 37px;
			padding-left: 67px;
			margin-right: 15px; }



		&-link {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			border-left: 2px solid transparent;
			transition: all .4s ease-out;
			color: $primary-gray;
			text-decoration: none;
			padding-left: 67px;
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			letter-spacing: 0.02em;
			line-height: 1rem;

			&_1 {
				background: url('../img/nav/item-1.svg') no-repeat;
				background-position: 19px center; }

			&_2 {
				background: url('../img/nav/item-2.svg') no-repeat;
				background-position: 19px center; }

			&_3 {
				background: url('../img/nav/item-3.svg') no-repeat;
				background-position: 19px center; }

			&_4 {
				background: url('../img/nav/item-4.svg') no-repeat;
				background-position: 19px center; }

			&_5 {
				background: url('../img/nav/item-5.svg') no-repeat;
				background-position: 19px center; }

			&_6 {
				background: url('../img/nav/item-6.svg') no-repeat;
				background-position: 19px center; }

			&_7 {
				background: url('../img/nav/item-7.svg') no-repeat;
				background-position: 19px center; }

			&_8 {
				background: url('../img/nav/item-8.svg') no-repeat;
				background-position: 19px center; }

			&_9 {
				background: url('../img/nav/item-9.svg') no-repeat;
				background-position: 19px center; }

			&_10 {
				background: url('../img/nav/item-10.svg') no-repeat;
				background-position: 22px center; }

			&_11 {
				background: url('../img/nav/item-11.svg') no-repeat;
				background-position: 13px center; }

			&_12 {
				background: url('../img/nav/item-12.svg') no-repeat;
				background-position: 19px center; }

			&_13 {
				background: url('../img/nav/item-13.svg') no-repeat;
				background-position: 19px center; }

			&_14 {
				background: url('../img/nav/item-14.svg') no-repeat;
				background-position: 19px center; }

			&_15 {
				background: url('../img/nav/item-15.svg') no-repeat;
				background-position: 23px center; } }



		&-inner {
			width: 923px;
			background-color: #fff;
			position: absolute;
			content: '';
			left: 100%;
			top: 0;
			padding: 24px;
			padding-top: 5px;
			z-index: 1000;
			box-shadow: 0 2px 10px rgba(0,0,0,.1);
			display: none;

			.catalog {
				margin-top: 0; } } }






	&__catalog {
		margin: 0;
		padding: 0;
		display: flex;

		&-item {
			display: flex;
			height: 100%;
			position: relative;

			&:hover .top-menu__sub-catalog {
				display: block; } } }

	&__link {
		position: relative;
		color: $top-menu_link;
		font-family: "RobotoBold",Arial, sans-serif;
		text-decoration: none;
		text-transform: uppercase;
		text-align: center;
		font-size: 12px;
		line-height: 1.2;
		height: 100%;
		width: 100%;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: center;
		letter-spacing: 0.14em;

		transition: background-color .4s ease-out;

		&:hover {
			background-color: $top-menu_link_hover; }

		&::after {
			position: absolute;
			content: '';
			height: 28px;
			width: 1px;
			background-color: #686868;
			right: 0; }


		&-catalog {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			color: $top-menu_link;
			text-decoration: none;
			background-color: $primary-color;
			font-size: 20px;
			font-family: "RobotoMedium", Arial, sans-serif;
			text-transform: none;
			letter-spacing: normal;
			height: 100%;
			max-width: 245px;
			box-sizing: border-box;
			padding-left: 60px;
			padding-right: 110px;
			transition: background-color .4s ease-out;

			&:hover {
				background-color: $primary-color_hover; }

			&::before {
				position: absolute;
				content: '';
				width: 16px;
				height: 14px;
				background: url('../img/hamburger.svg') transparent no-repeat center;
				left: 28px;
				top: 18px; } } }
	&__cart {
		width: 100%;

		&-container {
			display: flex;
			flex-direction: column; }

		&-icon {
			margin-right: 9px;
			width: 26px;
			height: 31px;

			svg {
				width: 26px;
				height: 31px; } }


		&-link {
			position: relative;
			display: block;
			color: $top-menu_link;
			text-decoration: none;
			font-size: 12px;
			line-height: 1;
			height: 100%;


			display: flex;
			align-items: center;
			padding-left: 10px;

			box-sizing: border-box;
			transition: background-color .4s ease-out;

			&:hover {
				background-color: $top-menu_link_hover; }

			&::after {
				position: absolute;
				content: '';
				height: 100%;
				width: 1px;
				background-color: #686868;
				left: -1px; } }

		&-text {
			color: $primary-color;
			text-transform: uppercase;
			font-family: "RobotoBold", Arial, sans-serif;
			font-size: 12px;
			letter-spacing: 0.14em;
			line-height: 0.9; }

		&-value {
			font-size: 12px;
			color: $cart-value; } } }

