.good {

	background-color: #fff;
	border: 1px solid $border;
	border-radius: 8px;
	overflow: hidden;
	padding: 5px 14px 14px 14px;
	background-color: #fff;

	display: flex;
	flex-direction: column;


	&-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		&__text-wrapper {
			width: 100%;
			display: flex;
			margin-bottom: 5px; }

		&_text_center {
			margin-left: auto;
			margin-right: auto; } }

	&__header {
		display: flex;
		margin-bottom: 5px; }

	&__art {
		font-family: "RobotoRegular", sans-serif;
		color: $secondary-gray;
		font-size: 12px;
		line-height: 1rem;
		letter-spacing: 0.02em;
		margin-right: auto;

		&-num {
			color: $primary-color; } }

	&__tab {
		cursor: pointer;
		width: 22px;
		height: 20px;
		background: url('../img/good__tab.svg') no-repeat center;

		&:hover {
			opacity: 0.7; } }


	&__compare {
		cursor: pointer;
		width: 22px;
		height: 20px;
		background: url('../img/good__compare.svg') no-repeat center;
		margin-left: 5px;

		&:hover {
			opacity: 0.7; } }

	&__review-link {
		color: $primary-color;
		text-decoration: underline;
		margin-left: 20px;
		position: relative;
		margin-bottom: 5px;

		&::before {
			position: absolute;
			content: '';
			width: 13px;
			height: 11px;
			background: url('../img/good__review-link.svg') no-repeat center;
			left: -20px;
			top: 4px; }

		&:hover {
			text-decoration: none; } }

	&__delivery-link {
		color: $primary-blue;
		text-decoration: underline;
		margin-left: 20px;
		position: relative;
		margin-bottom: 9px;

		&::before {
			position: absolute;
			content: '';
			width: 14px;
			height: 14px;
			background: url('../img/good__delivery-link.svg') no-repeat center;
			left: -20px; }

		&:hover {
			text-decoration: none; } }



	&__link {
		text-align: center; }


	&__img {
		width: 100%;
		max-width: 160px;

		height: auto;
		max-height: 142px;
		align-self: center; }



	&__title {
		font-family: "RobotoRegular", Arial, sans-serif;
		font-size: 13px;
		line-height: 1em;
		letter-spacing: 0.02em;
		color: $primary-gray;
		margin-top: 3px;
		font-weight: normal;
		margin-bottom: 14px; }

	&__price {
		font-family: "RobotoMedium", Arial, sans-serif;
		font-size: 18px;
		line-height: 0.9em;
		color: $primary-gray;
		margin-top: 0;
		margin-bottom: 12px; }

	&__button {
		width: 100%;
		line-height: 36px; }

	&__wrap {

		&-img {
			display: flex;
			align-items: center; }

		&-info {
			display: flex;
			flex-direction: column;
			margin-left: 30px; } }

	&__banner {
		max-width: 100%; }


	&-wrap {
		border-bottom: 1px solid $border;
		padding-bottom: 35px; }







	&_col_4 {
		width: 24%;
		box-sizing: border-box;
		margin-bottom: 1.33%;

		&:not(:nth-child(4n+4)) {
			margin-right: 1.33%; } }


	&_col_2_sm {



		@media screen and (max-width:992px) {
			width: 46%;
			box-sizing: border-box;
			margin-bottom: 1.33%; }

		&:not(:nth-child(4n+4)) {
			@media screen and (max-width:992px) {
				margin-right: 0; } }

		&:not(:nth-child(2n+2)) {
			@media screen and (max-width:992px) {
				margin-right: 1.33%; } } }

	&_col_1_sm {
		@media screen and (max-width:580px) {
			width: 100%;
			box-sizing: border-box;
			margin-bottom: 1.33%;
			margin-right: 0; } }


	&_no-border {
		border: none;
		padding-left: 0;
		padding-right: 0; }

	&__list {
		display: flex; }


	&-info {

		&__row {
			display: flex;

			&_margin-bottom_10 {
				margin-bottom: 10px; }

			&_margin-bottom_18 {
				margin-bottom: 18px; } }

		&__price {
			font-family: "RobotoBold", sans-serif;
			color: $primary-gray;
			line-height: 0.9;
			font-size: 32px;
			margin: 0;
			margin-right: 12px; }

		&__nds {
			margin-bottom: 15px;
			margin-top: 3px; }

		&_col_3 {
			width: 30%;

			@media screen and (max-width:992px) {
				width: 100%;
				margin-top: 20px; } }

		&__button {
			line-height: 54px;
			width: 100%;
			margin-bottom: 13px; }

		&__click {
			line-height: 54px;
			width: 100%;
			border: 1px solid $primary-color;
			background-color: transparent;
			color: $primary-color;
			margin-bottom: 13px;

			&:hover {
				background-color: $primary-color;
				color: #fff; } }

		&__inner {
			display: flex;
			margin-top: 10px;


			&-col {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				width: 50%;

				&_margin-bottom_10 > * {
					margin-bottom: 10px !important; }

				&_no-margin-top > * {
					margin-top: 0; }

				&_size_l {
					width: 60%; } } }

		&__column {
			display: flex;
			flex-direction: column; }

		&_align-center {
			align-items: center; }

		&_space-between {
			justify-content: space-between; }


		&_space-between {
			justify-content: space-between; } }

	&-present {
		width: 100%;
		display: flex;
		justify-content: space-between;
		margin-bottom: 21px;

		@media screen and (max-width:992px) {
			flex-direction: column; } } }



.good__wrap-img .good__img {
	max-width: 74px;
	max-height: 67px; }


