.help {

	&__text {
		font-family: "RobotoRegular", sans-serif;
		color: $primary-gray;
		line-height: 1.25rem;

		&_margin-bottom_30 {
			margin-bottom: 30px; }

		&_margin-bottom_40 {
			margin-bottom: 40px; }

		&_margin_25 {
			margin: 25px 0; } }

	&__sub-title {


		font-family: "RobotoMedium", Arial, sans-serif;
		font-size: 20px;
		line-height: 1;
		color: $primary-gray;
		font-weight: normal;
		margin-bottom: 20px;
		margin-top: 0;

		&_margin-bottom_15 {
			margin-bottom: 15px; }

		&_margin-bottom_25 {
			margin-bottom: 25px; } }


	&-section {
		padding: 30px 0 30px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		position: relative;

		&::after {
			position: absolute;
			content: '';
			width: 100%;
			height: 1px;
			background-color: $border;
			bottom: 0;
			left: 0; }

		&__list {
			padding-left: 20px;
			max-width: 617px;

			&-item {
				color: $primary-color;
				margin-bottom: 13px; }

			&-text {
				color: $primary-gray; } }

		&_no-border-bottom {
			&::after {
				position: absolute;
				content: '';
				width: 100%;
				height: 1px;
				background-color: transparent;
				bottom: 0;
				left: 0; } }

		&_m-wrap {
			@media screen and (max-width:580px) {
				flex-wrap: wrap; } }




		&__item {
			width: 45%;
			display: flex;
			flex-direction: column;
			margin-bottom: 15px;

			&:last-child {
				@media screen and (max-width:580px) {
					margin-bottom: 0; } }

			@media screen and (max-width:580px) {
				width: 100%;
				margin-bottom: 40px; }

			&_size_l {
				width: 47%;

				@media screen and (max-width:580px) {
					margin-bottom: 20px; } }

			&_size_s {
				width: 41%; } }



		&__item-descr {
			display: flex;
			flex-wrap: wrap; }

		&__item-time {
			display: flex;
			flex-direction: column;
			margin-right: auto;

			@media screen and (max-width:580px) {
				margin-bottom: 15px; } }


		&__item-mail {
			display: flex;
			flex-direction: column; }


		&__item-info {
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			color: #171615;
			line-height: 1rem;
			letter-spacing: 0.02em; }


		&__info-title {
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			color: $primary-gray;
			line-height: 1rem;
			letter-spacing: 0.02em;
			margin-bottom: 5px;

			&_size_s {
				font-size: 12px;
				color: $secondary-gray; } }

		&__info-phone {
			font-family: "RobotoBold", sans-serif;
			font-size: 28px;
			color: $primary-color;
			line-height: 1;
			margin-bottom: 5px; }

		&_block {
			display: block; }

		&__img-item {
			@media screen and (max-width:580px) {
				width: 100%; } }

		&__map {
			display: flex;
			justify-content: space-between;

			&-item {
				width: 45.2%;

				@media screen and (max-width:580px) {
					width: 100%; } }

			&-img {
				max-width: 100%;
				width: 100%;
				height: auto; }



			@media screen and (max-width:580px) {
				flex-direction: column;
				align-items: center; } }

		&__img {

			&-item {
				width: 52%; } }

		&:last-child {

			&::after {
				position: absolute;
				content: '';
				width: 100%;
				height: 1px;
				background-color: transparent;
				bottom: 0;
				left: 0; } } } }
