.material-list {
	display: flex;
	padding-left: 0;
	margin-bottom: 6px;
	margin-top: 6px;
	flex-wrap: wrap;

	&__item {
		padding-left: 8px;
		list-style: none; }

	&__title {
		font-family: "RobotoBold", sans-serif;
		font-size: 14px;
		letter-spacing: 0.02em;
		color: $primary-gray; }

	&__link {
		color: $primary-blue;
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		line-height: 1.2;
		letter-spacing: 0.02em;

		&:hover {
			text-decoration: none; } } }
