.sorter-brands {

	margin-bottom: 33px;

	&__title {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		color: $page-text;
		letter-spacing: 0.02em;
		margin-bottom: 25px; }

	&__inner {
		display: flex;
		margin-bottom: 25px;
		flex-wrap: wrap; }



	&__item {
		font-family: "RobotoBold", sans-serif;
		font-size: 14px;
		color: $primary-gray;
		text-transform: uppercase;
		text-decoration: none;
		margin-right: 12px;

		&:hover {
			text-decoration: underline; } }

	&__list {
		padding: 0;
		width: 100%;
		display: flex;
		flex-wrap: wrap;

		&-item {
			list-style: none;
			position: relative;
			padding-right: 30px;
			margin-bottom: 17px;


			&::after {
				position: absolute;
				content: '';
				width: 1px;
				height: 17px;
				background-color: $border;
				right: 15px;
				top: 0; } }


		&-link {
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			color: $primary-blue;




			&:hover {
				text-decoration: none; } } } }
