.popular {

	width: 100%;
	margin-bottom: 0;

	&_margin-top_50 {
		margin-top: 50px; }

	&__title {
		font-size: 18px; }

	&__list {
		width: 100%;
		display: flex;
		flex-wrap: wrap; }

	&__item {
		width: 102px;
		height: 45px;
		margin-bottom: 15px;
		margin-right: 22px;
		padding: 6px;
		box-sizing: border-box;
		background-color: #fff;
		overflow: hidden;

		display: flex;
		justify-content: center;
		align-items: center; }

	&__img {

		max-width: 100%;
		max-height: 100%; } }
