.sub-catalog {
	background-color: #fff;
	border: 1px solid $border;
	border-radius: 8px;
	width: 100%;
	padding: 15px;
	box-sizing: border-box;
	margin-bottom: 32px;


	&__list {
		padding-left: 0;
		display: flex;
		flex-wrap: wrap;
		position: relative;
		z-index: 1;
		padding-bottom: 20px;

		&::after {
			position: absolute;
			content: '';
			width: 100%;
			height: 1px;
			background-color: $border;
			bottom: 0;
			z-index: 2; } }

	&__item {
		list-style: none;
		width: 30%;
		margin-right: 5%;
		margin-bottom: 15px;

		@media screen and (max-width:992px) {
			width: 50%;
			margin-right: 0; }

		@media screen and (max-width:580px) {
			width: 100%; }


		&:nth-child(3n+3) {
			margin-right: 0; } }

	&__img {
		margin-right: 14px; }


	&__link {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		letter-spacing: 0.02em;
		line-height: 1.2;
		color: $primary-gray;
		text-decoration: none;
		display: flex;
		align-items: center;

		&:hover {
			text-decoration: underline; }

		&_margin_bottom_22 {
			margin-bottom: 22px; } } }
