.cart {

	&__header {
		display: flex;
		padding-bottom: 7px; }

	&__sum {
		margin-bottom: 10px; }

	&__sum-price {
		margin-bottom: 10px; }

	&__print {
		margin-left: auto;
		align-self: flex-start; }

	&__back {

		color: $primary-color;
		text-decoration: none;
		position: relative;
		padding-left: 29px;
		margin-bottom: 18px;

		&::before {
			position: absolute;
			content: '';
			width: 13px;
			height: 14px;
			background-color: $primary-color;
			background: url('../img/icon-back.svg') no-repeat center;
			top: 2px;
			left: 3px; } }

	&__issue {
		height: 54px;
		width: 270px;
		margin-top: 25px; }

	&__row {
		display: flex; }

	&__column {
		display: flex;
		flex-direction: column;
		justify-content: space-between; }


	&__result {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-right: 43px; }

	&__clear {
		color: $primary-color;
		text-decoration: none;
		position: relative;
		padding-left: 29px;

		&::before {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(45deg);
			top: 9px;
			left: 0; }

		&::after {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(-45deg);
			top: 9px;
			left: 0; } }

	&__item {
		display: flex;
		padding: 11px 0;
		border-bottom: 1px solid $border; }

	&__item-img {
		margin-right: 14px;
		width: 98px;
		height: 98px;
		border: 1px solid $border;
		box-sizing: border-box;
		border-radius: 8px;
		padding: 12px;
		display: flex;
		align-items: center;
		background-color: #fff; }

	&__search {
		background-color: transparent;
		border: 1px solid $border;
		border-radius: 8px;
		margin-top: 15px;
		box-shadow: inset 0 0 5px rgba(0,0,0,.1);
		margin-bottom: 20px; }

	&__descr {}

	&__tab {
		cursor: pointer;
		margin-right: 56px; }

	&__close {
		position: relative;
		width: 21px;
		height: 21px;
		cursor: pointer;

		&::before {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(45deg);
			top: 8px; }

		&::after {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(-45deg);
			top: 8px; } }

	&__controls {
		display: flex; }


	&__img {}

	&__title {}

	&__art {
		margin-top: 10px; }

	&__price {
		font-family: "RobotoMedium", sans-serif;
		font-size: 20px;
		color: $primary-gray; }

	&_padding {
		padding-bottom: 50px;
		border-bottom: 1px solid $border; }



	&_col_4 {
		max-width: 390px;
		width: 100%;
		display: flex; }

	&_col_2 {
		max-width: 150px;
		width: 100%;
		display: flex; }

	&_col_1 {
		max-width: 120px;
		width: 100%;
		display: flex; } }

