.filter {
	margin-left: 20px;
	margin-right: 13px;

	&-wrap {

		&_m-margin {
			@media screen and (max-width:992px) {
				margin-bottom: 40px; } } }


	&__button {
		width: 40px;
		height: 40px;
		background: url('../img/filter.svg') transparent no-repeat center;
		cursor: pointer; }


	&__settings {
		position: absolute;
		height: 20px;
		background-color: red; }

	&-inner {
		width: 100%;
		display: flex;
		background-color: #fff;
		border: 1px solid $border;
		border-radius: 8px;
		margin-top: 10px;
		padding: 10px 20px;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		position: relative; }

	&-inner__item {
		margin: 0; }

	&__label {
		color: $primary-gray;
		font-size: 16px;
		font-family: "RobotoBold", sans-serif;
		margin-right: 4px;

		&_margin {
			margin-right: 23px; } }

	&__select {
		appearance: none;
		border: none;
		width: 100%;
		height: 100%;
		font-size: 16px;


		&:focus {
			outline: none; } }

	&__select-container {
		height: 25px;
		width: 204px;
		margin-top: 3px;
		position: relative;

		margin-right: 60px;


		&::before {
			position: absolute;
			content: '';
			width: 14px;
			height: 8px;
			background: url('../img/filter__arrow.svg') no-repeat center;
			top: 7px;
			right: 0;
			pointer-events: none; } }


	&__brand {
		width: 72px; }

	&__range {
		margin-bottom: 0 !important; }

	&__price-label {
		margin-right: 42px; }


	&__price {
		color: #71717B;

		input[type=number] {
			text-align: center;
			padding: 0; } } }


.arrow {
	position: absolute;
	display: block;
	width: 0;
	height: 0;
	content: '';
	left: 30px;
	top: -15.5px;
	border: 8px solid transparent;
	border-bottom: 8px solid #fff;

	&::before {
		content: '';
		width: 13px;
		display: block;
		bottom: -4px;
		left: -2px;
		position: absolute;
		border-top: 1px solid $border;
		transform: rotate(45deg); }

	&::after {
		content: '';
		width: 13px;
		display: block;
		bottom: -4px;
		right: -2px;
		position: absolute;
		border-top: 1px solid $border;
		transform: rotate(-45deg); } }

