.link {
	color: $primary-blue;
	font-family: "RobotoRegular", sans-serif;
	font-size: 14px;
	line-height: 1.2;
	letter-spacing: 0.02em;
	margin-bottom: 15px;

	&:hover {
		text-decoration: none; }

	&_orange {
		display: inline-block;
		border-bottom: 1px dashed $primary-color;
		color: $primary-color;
		text-decoration: none;
		margin-bottom: 0;

		&:hover {
			border-bottom: 1px dashed transparent; } }

	&_icon-info {
		margin-left: 27px;
		position: relative;

		&::before {
			position: absolute;
			width: 20px;
			height: 20px;
			left: -27px;
			top: -2px;
			content: '';
			background: url('../img/link-info.svg') no-repeat center; } }

	&_icon-pdf {
		margin-left: 43px;
		position: relative;

		&::before {
			position: absolute;
			width: 24px;
			height: 26px;
			left: -38px;
			top: -2px;
			content: '';
			background: url('../img/icon-pdf.svg') no-repeat center; } }


	&_blue {
		display: inline-block;
		border-bottom: 1px dashed $primary-blue;
		color: $primary-blue;
		text-decoration: none;
		margin-bottom: 0;

		&:hover {
			border-bottom: transparent; } }



	&_icon {
		position: relative;
		padding-left: 25px;

		&::before {
			position: absolute;
			content: '';
			width: 16px;
			height: 20px;
			left: 0;
			background: url('../img/doc-icon.svg') no-repeat center; } }

	&_margin-right_40 {
		margin-right: {} }

	&_margin-bottom_30 {
		margin-right: 30px;
		display: inline-block; }

	&_no-margin-bottom {
		margin-bottom: 0; }

	&_margin-top_18 {
		margin-top: 18px; } }


.link-favorites {
	margin-left: 30px;
	position: relative;
	color: $primary-gray;
	text-decoration: none;

	&::before {
		position: absolute;
		content: '';
		left: -30px;
		top: 0;
		width: 22px;
		height: 19px;
		background: url('../img/good__tab.svg') transparent no-repeat center; }

	&_margin-right_20 {
		margin-right: 20px; } }

.link-compare {
	margin-left: 30px;
	position: relative;
	color: $primary-gray;
	text-decoration: none;


	&::before {
		position: absolute;
		content: '';
		left: -30px;
		border: 1px solid $border;
		top: 0;
		width: 17px;
		height: 17px;
		border-radius: 2px;
		background-color: transparent; } }


.link-cheaper {
	margin-left: 30px;
	position: relative;
	color: $primary-color;
	text-decoration: none;
	line-height: 0.9rem;
	font-family: "RobotoBold", sans-serif;
	max-width: 72px;

	&::before {
		position: absolute;
		content: '';
		left: -30px;
		top: 4px;
		width: 22px;
		height: 22px;
		background: url('../img/cheaper.svg') transparent no-repeat center; } }


.link-info {
	margin-right: 27px;
	position: relative;
	font-family: "RobotoBold", sans-serif;
	margin-bottom: 0;


	&::before {
		position: absolute;
		content: '';
		right: -27px;
		top: -1px;
		width: 18px;
		height: 18px;
		background: url('../img/icon-info.svg') no-repeat center; } }


