.news {
	width: 100%;


	&__list {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }


	&_border-bottom {
		position: relative;
		padding-bottom: 50px;

		&::after {
			position: absolute;
			content: '';
			width: 100%;
			height: 1px;
			bottom: 0;
			background-color: $border; } }

	&-card {
		width: 24%;
		margin-bottom: 30px;

		&__title {
			font-family: "RobotoBold", Arial, sans-serif;
			font-size: 14px;
			line-height: 1.25em;
			letter-spacing: 0.02em;
			color: $primary-gray;
			margin-bottom: 10px; }

		&__descr {
			font-family: "RobotoRegular",Arial, sans-serif;
			font-size: 14px;
			line-height: 1.25em;
			color: $page-text;
			margin-top: 10px;
			margin-bottom: 16px; }


		&__link {
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 4px; }

		&__img {
			width: 100%;
			height: auto;
			overflow: hidden; }


		&_col_2 {
			@media screen and (max-width:992px) {
				width: 49%; } }

		&_col_1_sm {
			@media screen and (max-width:580px) {
				width: 100%; } } } }



