}

.top-menu__catalog {
  margin: 0;
  padding: 0;
  display: flex;
}

.top-menu__catalog-item {
  display: flex;
  height: 100%;
  position: relative;
}

.top-menu__catalog-item:hover .top-menu__sub-catalog {
  display: block;
}

.top-menu__link {
  position: relative;
  color: #fff;
  font-family: "RobotoBold",Arial, sans-serif;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
  line-height: 1.2;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.14em;
  transition: background-color .4s ease-out;
}

.top-menu__link:hover {
  background-color: #4e4e4e;
}

.top-menu__link::after {
  position: absolute;
  content: '';
  height: 28px;
  width: 1px;
  background-color: #686868;
  right: 0;
}

.top-menu__link-catalog {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-decoration: none;
  background-color: #EC6D26;
  font-size: 20px;
  font-family: "RobotoMedium", Arial, sans-serif;
  text-transform: none;
  letter-spacing: normal;
  height: 100%;
  max-width: 245px;
  box-sizing: border-box;
  padding-left: 60px;
  padding-right: 110px;
  transition: background-color .4s ease-out;
}

.top-menu__link-catalog:hover {
  background-color: #f08d55;
}

.top-menu__link-catalog::before {
  position: absolute;
  content: '';
  width: 16px;
  height: 14px;
  background: url("../img/hamburger.svg") transparent no-repeat center;
  left: 28px;
  top: 18px;
}

.top-menu__cart {
  width: 100%;
}

.top-menu__cart-container {
  display: flex;
  flex-direction: column;
}

.top-menu__cart-icon {
  margin-right: 9px;
  width: 26px;
  height: 31px;
}

.top-menu__cart-icon svg {
  width: 26px;
  height: 31px;
}

.top-menu__cart-link {
  position: relative;
  display: block;
  color: #fff;
  text-decoration: none;
  font-size: 12px;
  line-height: 1;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 10px;
  box-sizing: border-box;
  transition: background-color .4s ease-out;
}

.top-menu__cart-link:hover {
  background-color: #4e4e4e;
}

.top-menu__cart-link::after {
  position: absolute;
  content: '';
  height: 100%;
  width: 1px;
  background-color: #686868;
  left: -1px;
}

.top-menu__cart-text {
  color: #EC6D26;
  text-transform: uppercase;
  font-family: "RobotoBold", Arial, sans-serif;
  font-size: 12px;
  letter-spacing: 0.14em;
  line-height: 0.9;
}

.top-menu__cart-value {
  font-size: 12px;
