  box-shadow: none;
  text-align: center;
  background-color: #EC6D26;
  color: #fff;
  font-family: "RobotoMedium", Arial, sans-serif;
  font-size: 16px;
  letter-spacing: 0.01em;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color .4s ease-out;
}

.button:hover {
  background-color: #f08d55;
  transition: background-color .4s ease-out;
}

.button__icon {
  width: 21px;
  height: 18px;
  margin-right: 10px;
}

.button__icon-feedback {
  width: 21px;
  height: 18px;
