.certificates {
  width: 100%;
  margin-bottom: 36px;
}

.certificates__list {
  display: flex;
  flex-wrap: wrap;
}

.certificates__item {
  border-radius: 3px;
  background-color: #fff;
}

.certificates__item_col_6 {
  width: 15.3%;
}

.certificates__item_col_6:not(:nth-child(6n+6)) {
  margin-right: 1.6%;
}

.certificates__item_col_5 {
  width: 18%;
  margin-right: 1.5%;
  box-sizing: border-box;
  padding: 8px 10px;
  border-radius: 4px;
  margin-bottom: 12px;
}

.certificates__img {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.tab__list {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: flex-start;
  padding: 0;
  margin-top: 0;
}

.tab__list::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  background-color: #D9D9D9;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.tab__item {
  list-style: none;
  background-color: #fff;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border: 1px solid #D9D9D9;
  display: flex;
  height: 44px;
  box-sizing: border-box;
}

.tab__item:not(:last-child) {
  margin-right: 8px;
}

.tab__item_active {
  position: relative;
  z-index: 3;
  border: 1px solid #EC6D26;
}

.tab__item_active .tab__link {
  color: #EC6D26;
  font-family: "RobotoBold", Arial, sans-serif;
}

.tab__link {
  text-decoration: none;
  font-family: "RobotoRegular", Arial, sans-serif;
  font-size: 14px;
  line-height: 1em;
  letter-spacing: 0.02em;
  color: #333333;
  height: 44px;
  box-sizing: border-box;
  padding: 13px 15px;
}

.tab__content {
  padding: 0;
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.tab__content-item {
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 100%;
  display: none;
}

.tab__content-item_active {
  display: flex;
}

.tab__title {
  margin-right: 30px;
}

.tab_icon-saw {
  padding-left: 40px;
  background: url("../img/saw.svg") #fff no-repeat;
  background-position: 19px center;
}

.good {
  background-color: #fff;
  border: 1px solid #D9D9D9;
  border-radius: 8px;
  overflow: hidden;
  padding: 5px 14px 14px 14px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}

.good-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.good-list__text-wrapper {
  width: 100%;
  display: flex;
  margin-bottom: 5px;
}

.good-list_text_center {
  margin-left: auto;
  margin-right: auto;
}

.good__header {
  display: flex;
  margin-bottom: 5px;
}

.good__art {
  font-family: "RobotoRegular", sans-serif;
  color: #818183;
  font-size: 12px;
  line-height: 1rem;
  letter-spacing: 0.02em;
  margin-right: auto;
}

.good__art-num {
  color: #EC6D26;
}

.good__tab {
  cursor: pointer;
  width: 22px;
  height: 20px;
  background: url("../img/good__tab.svg") no-repeat center;
}

.good__tab:hover {
  opacity: 0.7;
}

.good__compare {
  cursor: pointer;
  width: 22px;
  height: 20px;
  background: url("../img/good__compare.svg") no-repeat center;
  margin-left: 5px;
}

.good__compare:hover {
  opacity: 0.7;
}

.good__review-link {
  color: #EC6D26;
  text-decoration: underline;
  margin-left: 20px;
  position: relative;
  margin-bottom: 5px;
}

.good__review-link::before {
  position: absolute;
  content: '';
  width: 13px;
  height: 11px;
  background: url("../img/good__review-link.svg") no-repeat center;
  left: -20px;
  top: 4px;
}

.good__review-link:hover {
  text-decoration: none;
}

.good__delivery-link {
  color: #3A6597;
  text-decoration: underline;
  margin-left: 20px;
  position: relative;
  margin-bottom: 9px;
}

.good__delivery-link::before {
  position: absolute;
  content: '';
  width: 14px;
  height: 14px;
  background: url("../img/good__delivery-link.svg") no-repeat center;
  left: -20px;
}

.good__delivery-link:hover {
  text-decoration: none;
}

.good__link {
  text-align: center;
}

.good__img {
  width: 100%;
  max-width: 160px;
  height: auto;
  max-height: 142px;
  align-self: center;
}

.good__title {
  font-family: "RobotoRegular", Arial, sans-serif;
  font-size: 13px;
  line-height: 1em;
  letter-spacing: 0.02em;
  color: #333333;
  margin-top: 3px;
  font-weight: normal;
  margin-bottom: 14px;
}

.good__price {
  font-family: "RobotoMedium", Arial, sans-serif;
  font-size: 18px;
  line-height: 0.9em;
  color: #333333;
  margin-top: 0;
  margin-bottom: 12px;
}

.good__button {
  width: 100%;
  line-height: 36px;
}

.good__wrap-img {
  display: flex;
  align-items: center;
}

.good__wrap-info {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
}

.good__banner {
  max-width: 100%;
