.catalog-form__manufact-inner-item {
  list-style: none;
  padding: 6px 0;
  width: 50%;
}

.form-wrapper {
  display: none;
}

.checkbox {
  position: relative;
  display: flex;
  padding-left: 21px;
  color: #333333;
  cursor: pointer;
}

.checkbox_title {
  font-family: "RobotoBold", sans-serif;
  font-size: 14px;
  color: #333333;
  text-decoration: none;
}

.checkbox input[type=checkbox] + .checkbox__indicator {
  content: '';
  width: 13px;
  height: 13px;
  border: 1px solid #D9D9D9;
  border-radius: 2px;
  position: absolute;
  box-sizing: border-box;
  left: 0;
  top: 3px;
}

.checkbox input[type=checkbox]:checked + .checkbox__indicator::before {
  content: '';
  width: 9px;
  height: 9px;
  border-radius: 2px;
  position: absolute;
  background-color: #333333;
  left: 1px;
  top: 1px;
}

.checkbox__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.range__controls {
  position: relative;
  margin-bottom: 12px;
}

.range__value-wrap {
  display: flex;
  justify-content: space-between;
}

.range__toggle {
  position: absolute;
  width: 6px;
  height: 15px;
  content: '';
  background: url("../img/toggle.svg") no-repeat left center;
  z-index: 10;
  cursor: pointer;
}

.range__toggle-min {
  left: -3px;
}

.range__toggle-max {
  left: calc(100% - 3px);
}

.range__price-controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.range__price-labels {
  display: flex;
  align-items: center;
}

.range__scale {
  height: 4px;
  width: 100%;
  background-color: #D9D9D9;
}

.range__bar {
  background-color: #EC6D26;
  height: 4px;
  width: 100%;
}

.range__price-input {
  width: 63px;
  height: 30px;
  border: 1px solid #D9D9D9;
  border-radius: 4px;
  color: #333333;
  font-size: 14px;
  box-sizing: border-box;
  padding-left: 8px;
  text-align: left;
  margin-left: 10px;
  -moz-appearance: textfield;
}

.range__price-input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.range__price-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.range__price-input:focus {
  outline: none;
}

.range__price-input_max {
  margin-right: 8px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.45);
  z-index: 100;
}

.overlay__inner {
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.popup {
  width: 100%;
  max-width: 970px;
  border-radius: 8px;
  margin: 1.5em auto;
