.sorter {
	display: flex;
	align-items: center;
	margin: 15px 0;

	&__select {
		border: none;
		background-color: transparent;
		cursor: pointer;

		&-pseudo {
			display: flex;
			margin-right: 35px; }

		&-container {
			display: flex;
			align-items: center;

			&:not(:last-child) {
				margin-right: 35px; }

			&:last-child {
				margin-right: 40px; } } }

	&__form {
		display: flex;
		align-items: center; }

	&__label {
		font-family: "RobotoRegular", sans-serif;
		font-size: 12px;
		color: $page-text;
		letter-spacing: 0.02em;
		margin-right: 8px; } }

