.slider {
	position: relative;
	margin-top: 15px;
	margin-bottom: 8px;

	&__item {
		position: relative;
		height: 300px;
		background-color: #818183;
		border-radius: 4px;
		display: flex;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;


		@media screen and (max-width:580px) {
			height: 135px; }



		&-link {
			position: absolute;
			max-width: 253px;
			text-decoration: none;
			left: 50px;
			bottom: 35px;

			@media screen and (max-width:580px) {
				bottom: 15px;
				left: 15px; } }

		&-title {
			font-family: "RobotoMedium", sans-serif;
			font-size: 20px;
			line-height: 1;
			color: #fff;
			margin-bottom: 12px;

			@media screen and (max-width:580px) {
				font-size: 18px;
				max-width: 225px;
				margin-bottom: 0; } } }

	&__img {
		width: 100%;
		height: auto;
		max-width: 100%;
		min-height: 100%; } }






.slick-prev, .slick-next {
	display: none !important; }

.my-dots {
	position: absolute;
	bottom: 35px;
	right: 30px;
	display: flex;
	justify-content: flex-end;
	width: 100%;
	padding: 0;
	list-style: none;
	text-align: center;

	@media screen and (max-width:992px) {
    	display: none !important; } }



.my-dots li {
	position: relative;
	display: inline-block;
	width: 7px;
	height: 7px;
	margin: 0 6px;
	padding: 0;
	cursor: pointer; }


.my-dots li button {
	font-size: 0;
	line-height: 0;
	display: block;
	width: 7px;
	height: 7px;
	padding: 5px;
	cursor: pointer;
	color: transparent;
	border: 0;
	outline: none;
	background: transparent; }


.my-dots li button:hover,
.my-dots li button:focus {
	outline: none; }


.my-dots li button:hover:before,
.my-dots li button:focus:before {
	opacity: 1; }


.my-dots li button:before {
	position: absolute;
	top: 0;
	left: 0;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #c4c4c4;
	content: ''; }




.my-dots li.slick-active button:before {
	width: 7px;
	height: 7px;
	border-radius: 50%;
	background-color: #c4c4c4;
	content: '';
	top: -2px; }






