.button {
	white-space: nowrap;
	text-decoration: none;
	cursor: pointer;
	border-radius: 4px;
	border: none;
	box-sizing: border-box;
	box-shadow: none;
	text-align: center;
	background-color: $primary-color;
	color: #fff;
	font-family: "RobotoMedium", Arial, sans-serif;
	font-size: 16px;
	letter-spacing: 0.01em;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: background-color .4s ease-out;

	&:hover {
		background-color: $primary-color_hover;
		transition: background-color .4s ease-out; }

	&__icon {
		width: 21px;
		height: 18px;
		margin-right: 10px; }

	&__icon-feedback {
		width: 21px;
		height: 18px;
		margin-left: 10px; }


	&_feedback {
		height: 54px;
		max-width: 270px;

		@media screen and (max-width:460px) {
			width: 100%;
			max-width: 100%; } } }



