.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: rgba(0,0,0,.45);
	z-index: 100;

	&__inner {
		position: relative;
		overflow-x: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		z-index: 2; } }


.popup {
	width: 100%;
	max-width: 970px;
	border-radius: 8px;
	margin: 1.5em auto;
	position: relative;
	background-color: #fff;
	padding: 26px 40px;
	box-sizing: border-box;

	&__close {
		position: absolute;
		content: '';
		width: 24px;
		height: 24px;
		background: url('../img/cross.svg') no-repeat center;
		top: 28px;
		right: 40px;
		transition: opacity .4s ease-out;

		&:hover {
			cursor: pointer;
			opacity: 0.6; } }

	&_space-between {
		justify-content: space-between; }

	&__text {
		font-family: "RobotoMedium", sans-serif;
		font-size: 20px;
		color: $primary-gray;
		line-height: 1.2em;

		&:first-child {
			margin-bottom: 10px; } }

	&__subtitle {
		margin-bottom: 16px; }

	&__button {
		line-height: 54px;
		width: 100%;
		margin-top: 30px;
		margin-bottom: 22px; }

	&__back {
		align-self: center;
		margin-bottom: 0; }

	&__row {
		display: flex; }

	&__column {
		display: flex;
		flex-direction: column;

		&_margin-right_40 {
			margin-right: 40px; } }

	&__product {
		display: flex;
		max-width: 445px;
		width: 100%;
		margin-bottom: 30px; }

	&__buy {
		max-width: 185px;
		width: 100%;
		line-height: 36px;
		align-self: center;
		margin-left: auto;
		margin-right: 25px; }

	&__product-img {
		margin-right: 36px; }

	&__product-descr {}

	&__product-info {
		max-width: 390px;
		width: 100%;
		background-color: #F9F9F9;
		border-radius: 8px;
		padding: 35px 50px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column; } }

.popup__add .cart__item {
	&:last-child {
		border-bottom: 1px solid transparent; } }

.unscrollable {
	overflow: hidden; }

