.title {
	font-family: "RobotoMedium", Arial, sans-serif;
	font-size: 20px;
	line-height: 1;
	color: $primary-gray;
	margin-bottom: 20px;
	font-weight: normal;
	margin-top: 18px;

	&__container {
		display: flex;
		align-items: center;
		margin-bottom: 4px; }

	&_h1 {
		font-family: "RobotoBold", sans-serif;
		font-size: 28px;
		font-weight: normal;
		color: $title_h1;
		line-height: 1;
		margin-top: 14px; }

	&__description {
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		line-height: 1.2;
		letter-spacing: 0.02em;
		color: $primary-gray;
		margin-bottom: 24px; }

	&_popular {
		margin-right: 30px; }

	&_margin-bottom_10 {
		margin-bottom: 10px; }


	&_margin-bottom_25 {
		margin-bottom: 25px; }

	&_margin-bottom_35 {
		margin-bottom: 35px; }

	&_margin-bottom_40 {
		margin-bottom: 40px; }

	&_no-margin-top {
		margin-top: 0; } }



