.checkbox {

	position: relative;
	display: flex;
	padding-left: 21px;
	color: $primary-gray;
	cursor: pointer;

	&_title {
		font-family: "RobotoBold", sans-serif;
		font-size: 14px;
		color: $primary-gray;
		text-decoration: none; }

	input[type=checkbox] + .checkbox__indicator {

		content: '';
		width: 13px;
		height: 13px;
		border: 1px solid $border;
		border-radius: 2px;
		position: absolute;
		box-sizing: border-box;
		left: 0;
		top: 3px; }

	input[type=checkbox]:checked + .checkbox__indicator::before {

		content: '';
		width: 9px;
		height: 9px;
		border-radius: 2px;
		position: absolute;
		background-color: $primary-gray;
		left: 1px;
		top: 1px; }


	&__input {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		border: 0;
		padding: 0;
		white-space: nowrap;
		clip-path: inset(100%);
		clip: rect(0 0 0 0);
		overflow: hidden; } }


