.good-paginator {
	display: flex;
	width: 100%;
	position: relative;
	align-items: center;

	&__list {
		margin-left: auto;

		&_margin-right_25 {
			margin-right: 25px; } }

	&__item {
		font-family: "RobotoRegular", sans-serif;
		color: $page-text;
		text-decoration: none;
		padding: 2px;
		opacity: 1;
		transition: opacity .4s ease-out;

		&:hover {
			opacity: 0.7; }

		&_active {
			font-family: "RobotoRegular", sans-serif;
			color: $primary-color; }

		&:not(:last-child) {
			margin-right: 8px; } }

	&__border {
		&::after {
			position: absolute;
			content: '';
			width: 100%;
			height: 1px;
			bottom: 0;
			background-color: $border; } } }

