.certificates {

	width: 100%;
	margin-bottom: 36px;


	&__list {
		display: flex;
		flex-wrap: wrap; }


	&__item {

		border-radius: 3px;
		background-color: #fff;

		&_col_6 {
			width: 15.3%;

			&:not(:nth-child(6n+6)) {
				margin-right: 1.6%; } }


		&_col_5 {
			width: 18%;
			margin-right: 1.5%;
			box-sizing: border-box;
			padding: 8px 10px;
			border-radius: 4px;
			margin-bottom: 12px; }

		&_col_3 {
			@media screen and (max-width:580px) {
				width: 48%; } } }

	&__img {
		width: 100%;
		height: auto;
		overflow: hidden; } }
